
import { mapGetters, mapActions } from 'vuex';
import Toast from 'vux/src/components/toast/index.vue'

import pageHead from '@/component/page-header.vue';
import centerList from '@/component/center-list.vue';
import vuex from '@/store/index';

export default {
components: {
pageHead,
centerList,
Toast
},
name: 'centerInfo',
data() {
return {
formList: {
centerpic: './images/personal_image_avatar@2x.png',
centerNickName: '昵称',
centerPhone: '12345',
},
showToast: false,
errorText: '',
checkList: {
checkName: false,
checkPhone: false
}
};
},
methods: {
...mapActions([]),
checkPhoneNumber(number) {
let myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
if (myreg.test(number)) {
return true;
}
return false;
},
checkIsBlank(str) {
if(str.replace(/(^s*)|(s*$)/g, "").length <= 0) {
return false;
}
return true;
},
setNormal(check) {
this.checkList[check]= false;
}
},
watch: {
getIsSave(isSave){
if(isSave) {
let name = this.formList.centerNickName;
let phone = this.formList.centerPhone;
let phoneCheck = this.checkPhoneNumber(phone);
let nameCheck = this.checkIsBlank(name);
if(!phoneCheck || !nameCheck) {
this.errorText = '昵称、手机号填写错误'
this.checkList.checkName = true;
this.checkList.checkPhone = true;
if(phoneCheck) {
this.errorText = '昵称未填写';
this.checkList.checkPhone = false;
}else if(nameCheck){
this.errorText = '手机号错误请重新输入';
this.checkList.checkName = false;
}
this.showToast = true;
}else {
this.checkList.checkName = false;
this.checkList.checkPhone = false;
}
}
vuex.dispatch('AgetisSave', false);
}
},
computed: {
...mapGetters(['isSave']),
getIsSave(){
return this.isSave;
}
},
created() {},
mounted() {
let elem = document.querySelector('#vux_view_box_body');
setTimeout(() => {
elem.style.paddingBottom = '0';
}, 10);
}
};
